import React, { useMemo } from 'react';
import { View, Text, Pressable, ScrollView, StyleSheet, Platform, ViewStyle } from 'react-native';
import { BaseProduct } from '../types/scraper';
import { SortOption, SortField } from '../hooks/useSortedFoils';
import { colors } from '../constants/colors';
import { spacing } from '../constants/spacing';

interface FoilsTableProps {
  foils: BaseProduct[];
  sortOption: SortOption;
  onSort: (field: SortField) => void;
  isSelected: (foil: BaseProduct) => boolean;
  onFoilSelect: (foil: BaseProduct) => void;
}

interface SortIndicatorProps {
  isActive: boolean;
  direction?: 'asc' | 'desc';
}

const SortIndicator: React.FC<SortIndicatorProps> = ({ isActive, direction }) => {
  const columnWidths = useColumnWidths([]);  // Empty array since SortIndicator doesn't need dynamic widths
  const styles = useStyles(columnWidths);
  return (
    <View style={styles.sortIndicatorContainer}>
      <Text style={[
        styles.sortArrow,
        isActive && direction === 'asc' ? styles.sortArrowActive : styles.sortArrowInactive
      ]}></Text>
      <Text style={[
        styles.sortArrow,
        isActive && direction === 'desc' ? styles.sortArrowActive : styles.sortArrowInactive
      ]}></Text>
    </View>
  );
};

const useColumnWidths = (foils: BaseProduct[]) => {
  return useMemo(() => {
    const baseWidths = {
      brand: 100,      // Base width for "Brand" + sort indicator
      series: 120,     // Base width for "Series" + sort indicator
      model: 160,      // Base width for longer model names
      wingspan: 100,   // Width for "Wingspan" + numbers + unit
      area: 100,       // Width for "Area" + numbers + unit
      aspectRatio: 80, // Width for "AR" + numbers
      cord: 90,        // Width for "Cord" + numbers + unit
      type: 80,        // Width for "Type" + sort indicator
      price: 90,       // Width for "Price" + numbers
      link: 100,       // Width for "Product Link"
    };

    // Find maximum content lengths
    foils.forEach(foil => {
      const brandWidth = (foil.brand?.length || 0) * 8 + spacing.MEDIUM * 3;
      baseWidths.brand = Math.max(baseWidths.brand, brandWidth);
      
      const seriesWidth = (foil.series?.length || 0) * 8 + spacing.MEDIUM * 3;
      baseWidths.series = Math.max(baseWidths.series, seriesWidth);
      
      const modelWidth = (foil.model?.length || 0) * 8 + spacing.MEDIUM * 3;
      baseWidths.model = Math.max(baseWidths.model, modelWidth);
    });

    return baseWidths;
  }, [foils]);
};

const useStyles = (columnWidths: ReturnType<typeof useColumnWidths>) => {
  return StyleSheet.create({
    container: {
      flex: 1,
    },
    headerContainer: {
      backgroundColor: colors.LIGHT_GRAY,
      borderBottomWidth: 1,
      borderBottomColor: colors.BORDER,
      position: 'relative',
      top: 0,
      zIndex: 1,
    },
    headerRow: {
      flexDirection: 'row',
    },
    row: {
      flexDirection: 'row',
      borderBottomWidth: 1,
      borderBottomColor: colors.BORDER,
    },
    cell: {
      padding: spacing.MEDIUM,
      justifyContent: 'center',
    },
    textCell: {
      alignItems: 'flex-start',
    },
    numericCell: {
      alignItems: 'flex-end',
    },
    sortIndicatorContainer: {
      flexDirection: 'column',
      alignItems: 'center',
      justifyContent: 'center',
      width: 16,
      marginLeft: spacing.SMALL,
    },
    sortArrow: {
      fontSize: 8,
      lineHeight: 8,
      height: 8,
      textAlign: 'center',
    },
    sortArrowActive: {
      color: '#007AFF', // Use your app's primary color
    },
    sortArrowInactive: {
      color: '#C7C7CC', // Light gray color for inactive state
    },
    headerContent: {
      flexDirection: 'row',
      alignItems: 'center',
      justifyContent: 'space-between',
      width: '100%',
      paddingRight: spacing.SMALL,
    },
    headerNumericContent: {
      flexDirection: 'row',
      alignItems: 'center',
      justifyContent: 'flex-end',
      width: '100%',
      gap: spacing.SMALL,
    },
    headerCell: {
      backgroundColor: colors.LIGHT_GRAY,
      padding: spacing.SMALL,
      borderBottomWidth: 1,
      borderBottomColor: colors.BORDER,
    },
    headerText: {
      fontWeight: 'bold',
      color: colors.TEXT_PRIMARY,
      flex: 1,
    },
    numericText: {
      textAlign: 'right',
      width: '100%',
    },
    selectedRow: {
      backgroundColor: colors.SELECTED_ROW,
    },
    cellContent: {
      width: '100%',
    },
    link: {
      color: colors.LINK,
      textDecorationLine: 'underline',
    },
    tableBody: {
      flex: 1,
    },
    brandCell: {
      width: columnWidths.brand,
      flexShrink: 0,
      flexGrow: 0,
    },
    seriesCell: {
      width: columnWidths.series,
      flexShrink: 0,
      flexGrow: 0,
    },
    modelCell: {
      width: columnWidths.model,
      flexShrink: 0,
      flexGrow: 0,
    },
    wingspanCell: {
      width: columnWidths.wingspan,
      flexShrink: 0,
      flexGrow: 0,
    },
    areaCell: {
      width: columnWidths.area,
      flexShrink: 0,
      flexGrow: 0,
    },
    arCell: {
      width: columnWidths.aspectRatio,
      flexShrink: 0,
      flexGrow: 0,
    },
    cordCell: {
      width: columnWidths.cord,
      flexShrink: 0,
      flexGrow: 0,
    },
    typeCell: {
      width: columnWidths.type,
      flexShrink: 0,
      flexGrow: 0,
    },
    priceCell: {
      width: columnWidths.price,
      flexShrink: 0,
      flexGrow: 0,
    },
    linkCell: {
      width: columnWidths.link,
      flexShrink: 0,
      flexGrow: 0,
    },
  });
};

export const FoilsTable: React.FC<FoilsTableProps> = ({
  foils,
  sortOption,
  onSort,
  isSelected,
  onFoilSelect,
}) => {
  const columnWidths = useColumnWidths(foils);
  const styles = useStyles(columnWidths);
  
  const getSortIndicator = (field: SortField) => {
    const isActive = sortOption.field === field;
    return <SortIndicator isActive={isActive} direction={isActive ? sortOption.direction : undefined} />;
  };

  const renderHeaderContent = (text: string, field: SortField, isNumeric: boolean = false) => (
    <View style={isNumeric ? styles.headerNumericContent : styles.headerContent}>
      <Text style={[styles.headerText, isNumeric && styles.numericText]}>{text}</Text>
      {getSortIndicator(field)}
    </View>
  );

  const renderRow = (foil: BaseProduct) => {
    const rowKey = `row-${foil.brand}-${foil.model}`.toLowerCase().replace(/\s+/g, '-');
    const selected = isSelected(foil);
    
    return (
      <Pressable
        key={rowKey}
        onPress={() => onFoilSelect(foil)}
        style={[styles.row, selected && styles.selectedRow]}
      >
        <View style={[styles.cell, styles.textCell, styles.brandCell]}>
          <Text>{foil.brand}</Text>
        </View>
        <View style={[styles.cell, styles.textCell, styles.seriesCell]}>
          <Text>{foil.series}</Text>
        </View>
        <View style={[styles.cell, styles.textCell, styles.modelCell]}>
          <Text>{foil.model}</Text>
        </View>
        <View style={[styles.cell, styles.numericCell, styles.wingspanCell]}>
          <Text style={styles.numericText}>{foil.specs.wingspan || '-'}</Text>
        </View>
        <View style={[styles.cell, styles.numericCell, styles.areaCell]}>
          <Text style={styles.numericText}>{foil.specs.area || '-'}</Text>
        </View>
        <View style={[styles.cell, styles.numericCell, styles.arCell]}>
          <Text style={styles.numericText}>{foil.specs.aspectRatio?.toFixed(1) || '-'}</Text>
        </View>
        <View style={[styles.cell, styles.numericCell, styles.cordCell]}>
          <Text style={styles.numericText}>{foil.specs.cord || '-'}</Text>
        </View>
        <View style={[styles.cell, styles.textCell, styles.typeCell]}>
          <Text>{foil.type}</Text>
        </View>
        <View style={[styles.cell, styles.numericCell, styles.priceCell]}>
          <Text style={styles.numericText}>{foil.price ? `$${foil.price}` : '-'}</Text>
        </View>
        <View style={[styles.cell, styles.textCell, styles.linkCell]}>
          <Text 
            style={styles.link}
            onPress={() => Platform.OS === 'web' ? window.open(foil.sourceUrl, '_blank') : null}
          >
            View
          </Text>
        </View>
      </Pressable>
    );
  };

  return (
    <View style={styles.container}>
      <ScrollView horizontal>
        <View>
          <View style={styles.headerContainer}>
            <View style={styles.headerRow}>
              <Pressable style={[styles.cell, styles.headerCell, styles.brandCell]} onPress={() => onSort('brand')}>
                {renderHeaderContent('Brand', 'brand')}
              </Pressable>
              <Pressable style={[styles.cell, styles.headerCell, styles.seriesCell]} onPress={() => onSort('series')}>
                {renderHeaderContent('Series', 'series')}
              </Pressable>
              <Pressable style={[styles.cell, styles.headerCell, styles.modelCell]} onPress={() => onSort('model')}>
                {renderHeaderContent('Model', 'model')}
              </Pressable>
              <Pressable style={[styles.cell, styles.headerCell, styles.wingspanCell]} onPress={() => onSort('wingspan')}>
                {renderHeaderContent('Wingspan', 'wingspan', true)}
              </Pressable>
              <Pressable style={[styles.cell, styles.headerCell, styles.areaCell]} onPress={() => onSort('area')}>
                {renderHeaderContent('Area', 'area', true)}
              </Pressable>
              <Pressable style={[styles.cell, styles.headerCell, styles.arCell]} onPress={() => onSort('aspectRatio')}>
                {renderHeaderContent('AR', 'aspectRatio', true)}
              </Pressable>
              <Pressable style={[styles.cell, styles.headerCell, styles.cordCell]} onPress={() => onSort('cord')}>
                {renderHeaderContent('Cord', 'cord', true)}
              </Pressable>
              <Pressable style={[styles.cell, styles.headerCell, styles.typeCell]} onPress={() => onSort('type')}>
                {renderHeaderContent('Type', 'type')}
              </Pressable>
              <Pressable style={[styles.cell, styles.headerCell, styles.priceCell]} onPress={() => onSort('price')}>
                {renderHeaderContent('Price', 'price', true)}
              </Pressable>
              <View style={[styles.cell, styles.headerCell, styles.linkCell]}>
                {renderHeaderContent('Product Link', 'link')}
              </View>
            </View>
          </View>
          <ScrollView style={styles.tableBody}>
            {foils.map(renderRow)}
          </ScrollView>
        </View>
      </ScrollView>
    </View>
  );
};